section {
    padding-top: 4vh;
    height: 96vh;
    margin: 0 5rem;
    box-sizing: border-box;
    min-height: fit-content;
}

.section-container {
    display: flex;
}

.intro {
    display: flex;
    justify-content: center;
    gap: 2rem;
    height: 80vh;
    background-color: rgb(128, 128, 128, 0.6);
    margin-top: 30px;
    border-radius: 10px;
}

.section_pic_container {
    display: flex;
    height: 500px;
    width: 500px;
    align-items: center;

    img {
        border-radius: 50%;
        width: 80%;
        height: 80%;
        border: 5px solid white;
    }
}

.section_text {
    align-self: center;
    text-align: center;
}

.section_text p {
    font-weight: 600;
    font-size: 40px;
}

.section_text_p1 {
    text-align: center;
    color: rgb(187, 186, 186);
}

.section_text_p2 {
    font-size: 50px;
    margin-bottom: 1rem;
}

.title {
    font-size: 60px;
    text-align: center;
    font-weight: 900;
}

.button_container {
    display: flex;
    gap: 20px;
    margin: 50px;
}

.btn-color-1 {
    border-radius: 40px;
    width: 160px;
    height: 50px;
    font-size: 20px;
    font-weight: 600;
}

.btn-color-2 {
    border-radius: 40px;
    width: 160px;
    height: 50px;
    font-size: 20px;
    font-weight: 600;
}